<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item label="ICCID" prop="iccid">
                  <el-input
                    v-model="qryParam.iccid"
                    :placeholder="inputPh('ICCID')"
                    maxlength="20"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="MSISDN" prop="msisdn">
                  <el-input
                    v-model="qryParam.msisdn"
                    :placeholder="inputPh('MSISDN')"
                    maxlength="20"
                  >
                    <ct-async-select
                      slot="prepend"
                      v-model="qryParam.areaCode"
                      url="/user/areaCodeList"
                      :placeholder="$t('accountMag.areaCode')"
                      method="get"
                      strkv
                      style="width: 80px"
                    ></ct-async-select>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="IMSI" prop="imsi">
                  <el-input
                    v-model="qryParam.imsi"
                    :placeholder="inputPh('IMSI')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('dataUsage.billCycle')" prop="billCycle">
                  <BillCycleSelect v-model="qryParam.billCycle" />
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('dataUsage.usageType')" prop="qryType">
                    <ct-select
                      v-model="qryParam.qryType"
                      :placeholder="selectPh('dataUsage.usageType')"
                      :options="qryTypeOptions"
                    >
                    </ct-select>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item>
              <div class="search-button-group">
                <el-dropdown
                  split-button
                  type="primary"
                  @click="querySimCardListForPage(false)"
                  >{{ $t('common.query') }}
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="showUpload = true">{{
                      $t('common.batchQuery')
                    }}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
        <div class="tool-button-group">
          <BatchExport
            :query-id="2200"
            :params="simListExportParam"
            :disabled="tableData.length === 0"
          ></BatchExport>
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        height="100%"
        width="100%"
        :data="tableData"
        :border="true"
      >
        <el-table-column
          prop="offerName"
          :label="$t('dataUsage.offerName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="iccid"
          label="ICCID"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="msisdn"
          label="MSISDN"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="imsi"
          label="IMSI"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="qryType"
          :label="$t('dataUsage.usageType')"
          :formatter="formatQueryType"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="offerTotalDesc"
          :label="$t('dataUsage.totalUsage')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="offerUsageDesc"
          :label="$t('dataUsage.usage')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="offerRemainDesc"
          :label="$t('dataUsage.remain')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <BatchUpload
      v-if="showUpload"
      :visible.sync="showUpload"
      :query-id="2100"
      :is-xls="true"
      :param="{ custId: $store.state.user.user.custId }"
      :max-file-size="10 * 1024 * 1024"
      :title="$t('dataUsage.batchTitle1')"
    >
      <template #tip>
        1、{{ $t('batchUpload.uploadTip1xls') }}<br />
        2、{{ $t('batchUpload.uploadTip2') }}<br />
        3、{{ $t('batchUpload.uploadTip3') }}<br />
        4、{{ $t('batchUpload.uploadTip4Max10000') }}
      </template>
    </BatchUpload>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
import moment from 'moment'
import BatchUpload from '@/components/BatchUpload'
import BatchExport from '@/components/BatchExport'
const defaultForm = {
  // iccid: '89852350922000190008',
  iccid: '',
  areaCode: '',
  // msisdn: '883130200001488',
  msisdn: '',
  imsi: '',
  billCycle: moment(new Date()).format('YYYYMM'),
  qryType: '3',
}
export default {
  components: {
    BatchUpload,
    BatchExport,
  },
  mixins: [langMixin],
  data() {
    return {
      showUpload: false,
      powerSearch: false,
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      qryParam: { ...defaultForm },
    }
  },
  computed: {
    qryTypeOptions() {
      return [
        {
          label: this.$t('dataUsage.data'),
          value: '3',
        },
        {
          label: this.$t('dataUsage.voice'),
          value: '1',
        },
        {
          label: this.$t('dataUsage.sms'),
          value: '2',
        },
        {
          label: this.$t('dataUsage.all'),
          value: '0',
        },
      ]
    },
    simListExportParam() {
      const { billCycle } = this.qryParam
      return {
        ...this.qryParam,
        msisdn: this.accNum,
        billMonth: billCycle,
      }
    },
    accNum() {
      const { areaCode, msisdn } = this.qryParam
      if (msisdn) {
        return areaCode + msisdn
      }
      return ''
    },
    isValidate() {
      const numStr = this.accNum + this.qryParam.iccid + this.qryParam.imsi
      return numStr.length > 0
    },
  },
  methods: {
    // 查询业务配置
    async querySimCardListForPage(pageChangeFlag) {
      if (!this.isValidate) {
        this.$message.error(
          'Processing failed,ICCID MSISDN or IMSI cannot be empty at the same time'
        )
        return
      }
      try {
        this.loading = true
        if (!pageChangeFlag) {
          this.page.pageIndex = 1
          this.page.pageSize = 10
        }
        const params = { ...this.qryParam, accNum: this.accNum, ...this.page }
        const res = await fetch('/usageData/queryOfferUsage', params, {
          method: 'post',
        })
        this.tableData = res.list || []
        this.page.total = res.total
        this.loading = false
      } catch (e) {
        this.loading = false
      }
    },
    toDetail(msisdn) {
      this.$router.push({ path: 'simDetail', query: { msisdn } })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.querySimCardListForPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.querySimCardListForPage(true)
    },
    // 重置
    reset() {
      this.qryParam = { ...defaultForm }
      this.page = {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      }
      this.tableData = []
    },
    formatQueryType(row, column) {
      // 根据 row.qryType 的值查找对应的 label
      const option = this.qryTypeOptions.find((opt) => opt.value === row.qryType)
      return option ? option.label : '未知'
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.container {
  background-color: white !important;
  &-form {
    padding: 0px;
    overflow: hidden;
  }
}
</style>
